import { createSelector } from 'reselect';

const getCards = state => state.Cards.cards;
const getAccounts = state => state.Accounts.accounts;
export const getSelectedCardNumber = state => state.Cards.selectedCardNumber;

export const getSelectedCard = createSelector(
  [getCards, getSelectedCardNumber],
  (cards, selectedCardNumber) => {
    if (cards) {
      return cards.filter(card => card.cardNumber === selectedCardNumber)[0];
    }
  },
);
export const getSelectedCardProductCode = createSelector(
  [getSelectedCard],
  card => {
    if (card) return card.produitCarte;
  },
);
export const getSelectedCardBalance = createSelector(
  [getSelectedCard, getAccounts],
  (selectedCard, accounts) => {
    if (selectedCard) {
      let cardAccount = accounts.filter(
        account => selectedCard.accountNumber === account.accountNumber,
      )[0];
      return cardAccount ? cardAccount.availableBalance : '';
    }
  },
);
