import React, { createContext } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import Header from '../Navigation/Header/Header';
import SideMenu from '../Navigation/SideMenu/SideMenu';
import { t } from '../../i18n';
import { CONNECTED_FOOTER_NAMESPACE } from './constants';
import ScrollButton from '../ScrollUpButton/ScrollUpButton';

const ConnectedLayoutContext = createContext({
  showMenu: true,
  toggleMenu: () => {},
});
const MainContent = styled.div.attrs(props => ({
  className: `bcp-content ${props.showMenu ? '' : 'no-margin'}`,
}))``;

class ConnectedLayout extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      showMenu: true,
      toggleMenu: this._toggleMenu,
    };
  }
  _toggleMenu = () => {
    this.setState({ showMenu: !this.state.showMenu });
  };
  render() {
    return (
      <ConnectedLayoutContext.Provider value={this.state}>
        <div className="page-header">
          <Header />
          <SideMenu />
        </div>
        <MainContent showMenu={this.state.showMenu}>
          {this.props.children}
          <ScrollButton showAt={20} speed={2000} easing="easeInOutSine">
            <span className="scroll-to-top" />
          </ScrollButton>
        </MainContent>
        <div className="page-footer">
          <div className="footer-interne">
            <ul>
              <li> {t(`${CONNECTED_FOOTER_NAMESPACE}copyrightTitle`)} </li>
              <li>
                <Link to="#">
                  {t(`${CONNECTED_FOOTER_NAMESPACE}legalNotice`)}
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </ConnectedLayoutContext.Provider>
    );
  }
}
export const ConnectedLayoutConsumer = ConnectedLayoutContext.Consumer;
export default ConnectedLayout;
